body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.table-header {
  display: flex;
  flex-direction: row;
  align-content: center;
  align-items: center;
  justify-content: space-between;
}

.block {
  margin:10px;
  width: 358px;
}
table {
  border-collapse: collapse;
}
 td {
  padding-left: 24px;
  border: 0px solid black;
}

.modal {
  position: absolute;
  background-color: #fff;
  padding: 8px;
}
.list {
  display: flex;
  flex-direction: column;
  /* padding: 4px; */
}
.list button{
  margin: 4px 0;
  border-radius: 6px;
  border: none;
}
.buttons {
  display: flex;
  gap:8px;
}
h3, h2 {
  margin: 0;
  padding: 0;
}
th {
  font-weight: 500;
}
.selected {
  outline: 1px solid #4587ED;
}
h3 {
  width:  208px;
  height:  38px;
  padding: 8px 12px 8px 12px;
  gap: 33px;
  border-radius: 12px 12px 0px 0px;
  opacity: 0px;
  background-color: #353D4B;
  color: #FFFFFF;

}

.button  {
  background-color: #4587ED;
  border: none;
  border-radius: 6px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 4px ;
}
.button:hover  {
  background-color: #3573D2;
}
.button:active  {
  background-color: #225CB5;
}